@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(20px); }
  100% {
    opacity: 1;
    transform: translateY(0px); } }

@keyframes fadeInDownList {
  0% {
    opacity: 0;
    transform: translateY(10px); }
  100% {
    opacity: 1;
    transform: translateY(0px); } }

@keyframes hexagonAnimation {
  0% {
    transform: rotate(0deg) scale(0.5);
    opacity: 0; }
  100% {
    transform: rotate(45deg) scale(1);
    opacity: 1; } }

@keyframes centerVerticalLineAnimation {
  0% {
    transform: rotate(-45deg) scale(0.5);
    opacity: 0; }
  100% {
    transform: rotate(0deg) scale(1);
    opacity: 1; } }

@keyframes leftVerticalLineAnimation {
  0% {
    left: calc(50% - 3px);
    opacity: 0; }
  1% {
    opacity: 1; }
  100% {
    left: calc(50% - 23px);
    opacity: 1; } }

@keyframes letterALineAnimation {
  0% {
    width: 0; }
  100% {
    width: 20px; } }

@keyframes letterHLineAnimation {
  0% {
    width: 0; }
  100% {
    width: 14px; } }

@keyframes rightVerticalLineAnimation {
  0% {
    right: calc(50% - 3px);
    opacity: 0; }
  1% {
    opacity: 1; }
  100% {
    right: calc(50% - 23px);
    opacity: 1; } }

@keyframes arrowDrawIn {
  from {
    transform: translateY(-20px);
    stroke-dashoffset: 85; }
  to {
    transform: translateY(0px);
    stroke-dashoffset: 0; } }

@media (min-width: 768px) {
  @keyframes leftVerticalLineAnimation {
    0% {
      left: calc(50% - 6px);
      opacity: 0; }
    1% {
      opacity: 1; }
    100% {
      left: calc(50% - 43px);
      opacity: 1; } }
  @keyframes letterALineAnimation {
    0% {
      width: 0; }
    100% {
      width: 40px; } }
  @keyframes letterHLineAnimation {
    0% {
      width: 0; }
    100% {
      width: 28px; } }
  @keyframes rightVerticalLineAnimation {
    0% {
      right: calc(50% - 6px);
      opacity: 0; }
    1% {
      opacity: 1; }
    100% {
      right: calc(50% - 43px);
      opacity: 1; } }
  @keyframes arrowDrawIn {
    from {
      transform: translateY(-20px);
      stroke-dashoffset: 85; }
    to {
      transform: translateY(0px);
      stroke-dashoffset: 0; } } }

.logo {
  width: 100px;
  height: 100px;
  margin-top: 40px;
  position: relative; }
  @media (min-width: 768px) {
    .logo {
      width: 200px;
      height: 200px; } }
  .logo .hexagon {
    stroke: white;
    stroke-width: 18px;
    fill: transparent;
    overflow: unset;
    position: absolute;
    opacity: 0;
    -webkit-animation: hexagonAnimation 1s 0.5s forwards;
    animation: hexagonAnimation 1s 0.5s forwards; }
    @media (min-width: 768px) {
      .logo .hexagon {
        stroke-width: 18px; } }
  .logo .center-v-line {
    width: 6px;
    height: 40px;
    background: white;
    position: absolute;
    left: calc(50% - 3px);
    top: calc(50% - 20px);
    opacity: 0;
    -webkit-animation: centerVerticalLineAnimation 1s 0.5s forwards;
    animation: centerVerticalLineAnimation 1s 0.5s forwards; }
    @media (min-width: 768px) {
      .logo .center-v-line {
        width: 12px;
        height: 80px;
        left: calc(50% - 6px);
        top: calc(50% - 40px); } }
  .logo .left-v-line {
    width: 6px;
    height: 40px;
    background: white;
    position: absolute;
    left: calc(50% - 23px);
    top: calc(50% - 20px);
    opacity: 0;
    -webkit-animation: leftVerticalLineAnimation 0.5s 1.5s forwards;
    animation: leftVerticalLineAnimation 0.5s 1.5s forwards; }
    @media (min-width: 768px) {
      .logo .left-v-line {
        width: 12px;
        height: 80px;
        left: calc(50% - 46px);
        top: calc(50% - 40px); } }
  .logo .right-v-line {
    width: 6px;
    height: 40px;
    background: white;
    position: absolute;
    right: calc(50% - 23px);
    top: calc(50% - 20px);
    opacity: 0;
    -webkit-animation: rightVerticalLineAnimation 0.5s 1.75s forwards;
    animation: rightVerticalLineAnimation 0.5s 1.75s forwards; }
    @media (min-width: 768px) {
      .logo .right-v-line {
        width: 12px;
        height: 80px;
        right: calc(50% - 46px);
        top: calc(50% - 40px); } }
  .logo .a-top-line {
    width: 0px;
    height: 6px;
    background: white;
    position: absolute;
    left: calc(50% - 20px);
    top: calc(50% - 20px);
    -webkit-animation: letterALineAnimation 0.8s 2s forwards;
    animation: letterALineAnimation 0.8s 2s forwards; }
    @media (min-width: 768px) {
      .logo .a-top-line {
        height: 12px;
        left: calc(50% - 40px);
        top: calc(50% - 40px); } }
  .logo .a-mid-line {
    width: 0px;
    height: 6px;
    background: white;
    position: absolute;
    left: calc(50% - 20px);
    top: calc(50% - 3px);
    -webkit-animation: letterALineAnimation 0.8s 2s forwards;
    animation: letterALineAnimation 0.8s 2s forwards; }
    @media (min-width: 768px) {
      .logo .a-mid-line {
        height: 12px;
        left: calc(50% - 40px);
        top: calc(50% - 6px); } }
  .logo .h-mid-line {
    width: 0px;
    height: 6px;
    background: white;
    position: absolute;
    right: calc(50% - 20px);
    top: calc(50% - 3px);
    -webkit-animation: letterHLineAnimation 0.6s 2.25s forwards;
    animation: letterHLineAnimation 0.6s 2.25s forwards; }
    @media (min-width: 768px) {
      .logo .h-mid-line {
        height: 12px;
        right: calc(50% - 40px);
        top: calc(50% - 6px); } }
  .logo.isFooterLogo {
    width: 50px;
    height: 50px;
    margin: 10px 0; }
    .logo.isFooterLogo .hexagon {
      stroke: white;
      stroke-width: 18px;
      fill: transparent;
      overflow: unset;
      -webkit-animation: unset;
      animation: unset;
      opacity: 1;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg); }
    .logo.isFooterLogo .center-v-line {
      width: 3px;
      height: 20px;
      background: white;
      position: absolute;
      left: calc(50% - 1.5px);
      top: calc(50% - 10px);
      -webkit-animation: unset;
      animation: unset;
      opacity: 1; }
    .logo.isFooterLogo .left-v-line {
      width: 3px;
      height: 20px;
      background: white;
      position: absolute;
      left: calc(50% - 11.5px);
      top: calc(50% - 10px);
      -webkit-animation: unset;
      animation: unset;
      opacity: 1; }
    .logo.isFooterLogo .right-v-line {
      width: 3px;
      height: 20px;
      background: white;
      position: absolute;
      right: calc(50% - 11.5px);
      top: calc(50% - 10px);
      -webkit-animation: unset;
      animation: unset;
      opacity: 1; }
    .logo.isFooterLogo .a-top-line {
      width: 10px;
      height: 3px;
      background: white;
      position: absolute;
      left: calc(50% - 10px);
      top: calc(50% - 10px);
      -webkit-animation: unset;
      animation: unset;
      opacity: 1; }
    .logo.isFooterLogo .a-mid-line {
      width: 10px;
      height: 3px;
      background: white;
      position: absolute;
      left: calc(50% - 10px);
      top: calc(50% - 1.5px);
      -webkit-animation: unset;
      animation: unset;
      opacity: 1; }
    .logo.isFooterLogo .h-mid-line {
      width: 6px;
      height: 3px;
      background: white;
      position: absolute;
      right: calc(50% - 10px);
      top: calc(50% - 1.5px);
      -webkit-animation: unset;
      animation: unset;
      opacity: 1; }



